import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { EditOrderComponent } from './edit-order/edit-order.component';

import { OrdersComponent } from './orders-index/orders.component';
import { OrdersParentComponent } from './orders-parent/orders-parent.component';


const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: 'seller', component: OrdersParentComponent },
    { path: 'seller/pending', component: OrdersComponent },
    { path: 'seller/processing', component: OrdersComponent },
    { path: 'seller/completed', component: OrdersComponent },
    { path: 'seller/declined', component: OrdersComponent },
    { path: 'seller/edit-order/:id', component: EditOrderComponent }
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrdersRoutingModule { }
