import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { OrdersService } from '../../../services/orders/orders.service';
import { ordersColumn } from './orders.column';
import { saveAs } from 'file-saver';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
import { NbDialogService, NbToastrService } from '@nebular/theme';

@Component({
    selector: 'app-orders',
    templateUrl: './orders.component.html',
    styleUrls: ['./orders.component.scss']
})
export class OrdersComponent implements OnInit {

    public orderColumn: any;
    public isLoading: boolean;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    lastMeta: string;
    loading = false;
    store_type_id;
    store_type;
    status;


    constructor(
        private orderService: OrdersService,
        private toaster: NbToastrService,
        private convertFilter: FilterToQueryService,
        private dialogService: NbDialogService,
        private router: Router,
        private sharedService: SharedApiServiceService
    ) {
        this.orderColumn = ordersColumn;
    }

    ngOnInit(): void {
        this.store_type = this.router.url.split('/')[2];
        this.status = this.router.url.split('/')[3];
        this.store_type_id = (this.store_type == 'stores') ? 1 : 2;
        this.getOrders();
    }

    getOrders(meta="") {
        this.isLoading = true;
        console.log(meta);
        meta = meta + (meta ? '&' : '') + "type_id=" + this.store_type_id+ "&status=" + this.status;
        console.log(meta);
        this.lastMeta = meta;
        this.orderService.getOrdersList(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res['total'];
            },
            error => {
                this.isLoading = false;
            },
            () => {
                this.isLoading = false;
            },
        );
    }

    filterChange(event: any) {
        this.getOrders(this.convertFilter.convertToQuery(event));
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['/orders/'+this.store_type+'/edit-order/' + event.data.id]);
                break;
        }
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'orders/export/excel?' + this.lastMeta : 'orders/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'orders.xlsx');
        }),
            error => {
                this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
            };
    }
}
