import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class OrdersService {

  constructor(private http: HttpClient) { }

  getOrdersList(meta?: any) {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/orders' + (meta ? ('?' + meta) : '');
    return this.http.get(url);
  }

  getParentOrdersList(meta?: any) {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/orders_parent' + (meta ? ('?' + meta) : '');
    return this.http.get(url);
  }

  getOrder(id: any) {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/orders/' + id;
    return this.http.get(url);
  }
}
